.charts-selection {
    display: flex;
    background-color: rgb(87, 87, 87);
    border: 10px solid #252525;
    height: 500px;
    width: 900px;
    padding: 10px;
  }
  
  .chart-selector {
    display: flex;
    flex-direction: column;
    flex: 1; /* Adjust the flex property to control the width of chart-selector */
  }
  
  /* Style the label inside the chart-selector */
  .chart-selector label {
    font-weight: bold;
  }
  
  .right-chart {
    flex: 2; /* Adjust the flex property to control the width of right-chart */
    margin-left: 10px; /* Add margin to create space between chart-selector and right-chart */
  }
  
  /* Style the select dropdown */
  .chart-selector select {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  /* Style the bar chart container (canvas) */
  .right-chart {
    box-sizing: border-box;
    border: 10px solid #252525;
    width: 100%;
    max-width: 500px; /* Adjust the maximum width as needed */
    height: auto; /* Maintain aspect ratio */
  }
  
  .stat-options {
    display: flex;
    flex-direction: column;
  }
  
  .stat-options label {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }
  
  .stat-options input[type="checkbox"] {
    margin-right: 5px;
  }