html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #000000;
  color: #fff;
  height: 100%;
  display: flex; /* Add display: flex to the body to center its content */
  align-items: center;
  justify-content: center; /* Center content horizontally and vertically */
}

.page {
  display: flex;
  flex-direction: column;
  height: 100vh; /* Set the page to the full viewport height */
  width: 100vw;
}

.logo {
  padding: 10px;
}

.buttonHolder {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  background-color: #303030;
  justify-content: space-between; /* Distribute buttons evenly */
  align-items: center;
  text-align: center;
  padding: 15px;
  height: 10px;
  width: 100%;
  border: 5px solid #202020;
}

.pageHolder {
  flex: 1; 
  overflow: auto; 
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  background-color: #303030;
  align-items: center; 
  text-align: center; 
  color: #fff; 
  border: 3px solid #ff0000a9; 
  width: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
  background: #242424;      
}
